// =========== pizzas
.transitionGroup-exit {
  opacity: 1;
}
.transitionGroup-exit-active {
  opacity: 0;
  transition: opacity 1000ms ease-in;
}


// ========== options
.transitionOptions-enter {
  
  opacity: 0.5;
  transform: scale(0.7);
}
.transitionOptions-enter-active {
  opacity: 1;
  transform: translateX(0);
  transition: opacity 700ms, transform 700ms;
}
.transitionOptions-exit {
  opacity: 1;
}
.transitionOptions-exit-active {
  opacity: 0;
  
  transform: scale(0.7);
  transition: opacity 700ms, transform 700ms;
}
// ===================================
.pop-up__message-enter {
  opacity: 0;
  transform: translateX(-200px);
}
.pop-up__message-enter-active {
  opacity: 1;
  transform: translateX(0px);
  transition: opacity 1300ms, transform 1300ms;
}
.pop-up__message-exit {
  opacity: 1;
  animation: show-loader 1300ms forwards;
  transform: translateX(0px);
}
.pop-up__message-exit-active {
}

@keyframes show-loader{
  0%{
    transform: translateX(0px);
    opacity: 1;
  }
  50%{
    transform: translateX(0px);
    opacity: 1;
  }
  100%{
    transform: translateX(-200px);
    opacity: 0;
  }
}
// =================================

.messageError-enter {
  opacity: 0.5;
  transform: translateY(-20px);
}
.messageError-enter-active {
  opacity: 1;
  transform: translateY(0px);
  transition: opacity 400ms, transform 400ms;
}
.messageError-exit {
  opacity: 1;
  transform: translateY(0px);
}
.messageError-exit-active {
  opacity: 0;
  transform: translateY(-20px);
  transition: opacity 400ms, transform 400ms;
}
