.container{
  margin-bottom: 20px;
  text-align: center;
  flex: 1 0 100%;
}
.title{
  font-size: 20px;
  font-weight: 600;
  margin-bottom: 20px;
}
.img {
}


@media (prefers-reduced-motion: no-preference) {
  .img {
    animation: img-float infinite 3s ease-in-out;
  }
}
@keyframes img-float {
  0% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(15px)
  }
  100% {
    transform: translateY(0px)
  }
}