.order{
  text-align: center;
  & h3{
    font-size: 35px;
    font-weight: 600;
    // background-color: $bodyButton;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 30px 60px -12px, rgba(0, 0, 0, 0.3) 0px 18px 36px -18px;
    padding: 20px;
    margin-bottom: 15px;
  }
  &__list{
    margin: 0px auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    &>li:not(:last-child){
      margin-bottom: 25px;
    }
  }

  & li{
    font-size: 20px;
    cursor: pointer;
    padding: 10px 20px;
    border-radius: 10px;

    &.active{
      background: black;
      color: white;
    }
}
}




