.container{
  width: 900px;
  border: 1px solid black;
  padding: 50px;
  border-radius: 20px;
  background-color: rgba(255, 255, 255, 0.959);
  box-shadow: rgb(38, 77, 48) 0px 20px 30px -10px;
}

.price{
  display: flex;
  align-items: center;
}
.priceText{
  font-size: 20px
}
.priceText span{
  color:chocolate;
  font-size: 22px;
}

.form{
  display: flex;
  flex-direction: column;
  align-items: center;
}
.form >*:not(:last-child){
  margin-bottom: 10px;
}
