@media(max-width:1000px){
  .container{
    width: 600px;
    padding: 20px 40px 40px 40px;
  }
}
@media(max-width:680px){
  .container{
    width: 400px;
  }
}
@media(max-width:480px){
  .container{
    padding: 10px 20px 20px 20px;
    margin: 0 auto;
    width:100%;
  }


}