@media(max-width: $mobile){
  .pizzas{
    &__title {
      font-size: 20px;
      margin-bottom: 25px;
      text-align: center;
    }
    &__list{
      grid-row-gap: 30px;
    }
    &__burger-options{
      display: block;
    }

  }
  .pizza{
    width: 150px;
    &__img {
      width: 120px;
      margin-bottom: 0px;
    }
    &__title {
      font-size: 15px;
      margin-bottom: 5px;
		}
    &__price{
      font-size: 18px;
      margin: 5px 0;
      // background-color: $bodyButton;
    }
    &__burger-options{
      display: block;
    }
    &__options {
      display: none;
		}
    &__body-price {
      margin-top: 7px;
      display: flex;
      flex-direction: column; //
      align-items: center; //
      justify-content: center; //
		}
  }
 .options {
  width: 210px;

  &__type {
    
    display: block;
    &>*:not(:last-child){
      margin-right: 0px;
    }
    & li{font-size: 16px}
      
  }

  &__size {
    display: block;
    &>*:not(:last-child){
      margin-right: 0px;
    }
    & li{font-size: 16px;}
  }

}
.button-add {
   height: 30px;
   
}

}

@media(max-width: $mobile-mini){
.pizzas{
    &__list{
      grid-template-columns: repeat(1, 1fr);
    }
  }
}