
.button{
  height: 50px;
  width: 50%;
  line-height: 50px;
  margin: 10px 0px;
  letter-spacing: 5px;
  color: white;
  border-radius: 10px;
  background-color: #929292;
  
}
.buttonAct{
  height: 50px;
  width: 50%;
  line-height: 50px;
  margin: 10px 0px;
  border-radius: 10px;
  box-shadow: 0 0 40px 40px #145547 inset, 0 0 0 0 #1d9f82;
  font-family: 'Montserrat', sans-serif;
  font-weight: bold;
  letter-spacing: 5px;
  color: white;
  transition: .15s ease-in-out;
}
.buttonAct:hover{
  box-shadow: 0 0 10px 0 #145547 inset, 0 0 10px 4px #23231a;
  color: #040404;
}
