}

.receipt__body {
  margin-top: 40px;
  
  
}
.receipt__paragraph {
  margin-bottom: 40px;
}
.receipt__row {
  display: flex;
  justify-content: space-between;
}
.receipt__right {
}
.receipt__left {
}
.receipt__productName {
}
.receipt__countPrice {
  display: flex;

}
.receipt__count {
  margin-right: 20px;
}
.receipt__price {
}
.button{
  text-align: center;
  height: 50px;
  width: 50%;
  line-height: 50px;
  margin: 10px 0px;
  border-radius: 10px;
  box-shadow: 0 0 40px 40px #145547 inset, 0 0 0 0 #1d9f82;
  font-family: 'Montserrat', sans-serif;
  font-weight: bold;
  letter-spacing: 5px;
  color: white;
  transition: .15s ease-in-out;
}
.button:hover{
  box-shadow: 0 0 10px 0 #145547 inset, 0 0 10px 4px #23231a;
  color: #040404;
}