.button{
  padding: 15px 0px;
  width: 100%;
  margin: 10px 0px;
  border-radius: 10px;
  box-shadow: 0 0 40px 40px gray inset, 0 0 0 0 #1d9f82;
  font-family: 'Montserrat', sans-serif;
  font-weight: bold;
  letter-spacing: 2px;
  color: white;
  transition: .15s ease-in-out;
}
.active{
  box-shadow: 0 0 10px 0 #145547 inset, 0 0 10px 4px #23231a;
  color: #040404;
}