.container{
  z-index: 50;
  position: absolute;
  top: 20px;
  left: 50%;
  background-color: white;
  border-radius: 30px;
  border: 1px solid black;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 30px 60px -12px inset, rgba(0, 0, 0, 0.3) 0px 18px 36px -18px inset;
}
.innerContainer{
  margin-top: 50px;
  position: relative;
  padding: 20px;
}
.text{
  font-size: 18px;
  line-height: 1.5;
}

.text span {
  color: red;
  font-weight: 600;
}
.buttons{
  margin: 0 auto;
  width: 200px;
}

.burger{
  position: absolute;
  top: -30px;
  right: 5px;

}
.burger span{}

@media(max-width: 600px){
  .container{
      left: 20%;
  }
}