.background{
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    display: none;
    background-color: rgba(0,0,0,0.4);
}

.active{
    display: flex;
    justify-content: center;
    align-items: center;
}
.message{
    padding: 15px 0px;
    background-color: rgba(255, 255, 255, 0.959);
    border-radius: 20px;
    box-shadow: rgb(38, 77, 48) 0px 20px 30px -10px;
    width: 300px;
}
.wrapper{
  display: flex;
  flex-direction: column;
  align-items: center;
  /* background-color: aqua; */
}
.title{
  width: 300px;
  text-align: center;
  font-weight: 600;
  font-size: 20px;
  margin-bottom: 20px;
}
.wrapper>:nth-child(2){
  box-shadow: rgba(12, 46, 18, 0.506) 0px 48px 100px 0px;
}
.wrapper>:nth-child(3){
  margin: 10px;
  font-size: 26px;
  /* background-color: aqua; */
}
.wrapper>:nth-child(4)>button{
  padding: 20px 30px;
  width: 150px;
}