.container{
  z-index: 50;
  position: fixed;
  top: 20px;
  left: 50%;
  background-color: white;
  border-radius: 30px;
  /* border: 1px solid black; */
  /* background-color: aqua; */
  box-shadow: rgba(50, 50, 93, 0.25) 0px 30px 60px -12px inset, rgba(0, 0, 0, 0.3) 0px 18px 36px -18px inset;
}
.innerContainer{
  padding: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.text{
  font-size: 18px;
  line-height: 1.5;
}
.button{
  display: block;
  text-align: center;
  padding: 15px 0px;
  width: 100%;
  margin: 10px 0px;
  border-radius: 10px;
  box-shadow: 0 0 40px 40px gray inset, 0 0 0 0 #1d9f82;
  font-family: 'Montserrat', sans-serif;
  font-weight: bold;
  letter-spacing: 2px;
  color: white;
  transition: .15s ease-in-out;
}
.button:hover{
  box-shadow: 0 0 10px 0 #145547 inset, 0 0 10px 4px #23231a;
  color: #040404;
}


@media(max-width: 600px){
  .container{
      left: 20%;
  }
}