.container{
  padding-top: 30px;
  position: relative;
  width: 100%;
  display: flex;
  flex-direction: column;
  
}
.messageError{
  width: 100%;
  position: absolute;
  top: 5px; 
  left: 50%;
}
.messageError p{
transform: translateX(-50%);
  font-size: 18px;
  font-weight: 400;
  color:red;
/* background: rgb(212, 189, 235); */
}

.invisible{
  visibility: hidden;
}

.input{
  text-align: center;
  border: 1px solid black;
  height: 50px;
  padding: 0 10px;
  width: 100%;
  font-size: 25px;
  border-radius:20px;
}
  /* Chrome, Safari, Edge, Opera */
.input::-webkit-outer-spin-button,
.input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
.input[type=number] {
  -moz-appearance:textfield;
}

@media(max-width:1000px){
  .input{
    font-size: 18px;
  }
}
@media(max-width:400px){
  .input{
    font-size: 16px;
  }
  .messageError{
  top: -4px; 
}
  .messageError p{
  transform: translateX(-50%);
  font-size: 16px;
  font-weight: 400;
  color:red;
}
}