.container{
  margin-top: 20px;
  text-align: center;
  font-size: 20px;
}
.message{
  margin-top: 20px;
}
.title{
  font-weight: 600;
  margin-bottom: 20px;
}

.logo {
  height: 40vmin;
}

.logo:hover {
  fill: gold;
}

@media (prefers-reduced-motion: no-preference) {
  .logo {
    animation: logo-float infinite 3s ease-in-out;
  }
}
@keyframes logo-float {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(10px)
  }
  100% {
    transform: translateY(0px)
  }
}
