.footer {
  
		&__logo {
      padding: 20px 0;
      position: relative;
      display:flex;
      align-items: center;
      justify-content: center;
      margin-bottom: 20px;
      &::before{
        content: "";
        position: absolute;
        background-color: $bodyButton;
        bottom: 5px;
        left: 0;
        width: 100%;
        height: 5%;
      }
      & p{
        margin-left: 10px;
        text-align: center;
      }
     
		}
    &__picture{
      width: 70px;
      flex: 0 0 70px;
      height: 70px;
    }

		&__info {
      display: flex;
		}

		&__column {
      text-align: center;
      width: 100%;
      

		}

		&__title {
      font-size: 18px;
      font-weight: 600;
      border: 1px solid black;
      height: 50px;
      background-color: $mainBackground;
      display: flex;
      align-items: center;
      justify-content: center;
      
		}
    &__subTitle-container{
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      height: 150px;
      background-color: rgb(27, 30, 30);
    }
		&__subtitle {
      font-size: 18px;
      color: $mainElement;
      margin-bottom: 10px;
      
		}
    &__contacts {
      text-align: center;
      font-size: 16px;
      letter-spacing: 3px;
    }
}

@media(max-width: 920px){

  .footer {
  
		&__info {
      display: grid;
      grid-template-columns: repeat(6, 1fr);
      grid-template-rows: repeat(2, 1fr);
      grid-column-gap: 0px;
      grid-row-gap: 0px;
      &>:nth-child(1){
        grid-area: 1 / 1 / 2 / 3;
      }
      &>:nth-child(2){
        grid-area: 1 / 3 / 2 / 5;
      }
      &>:nth-child(3){
        grid-area: 1 / 5 / 2 / 7;
      }
      &>:nth-child(4){
        grid-area: 2 / 1 / 3 / 4; 
      }
      &>:nth-child(5){
        grid-area: 2 / 4 / 3 / 7;
      }

      
		}
     
		
    &__picture{
    }

		&__info {
		}

		&__column {
		}

		&__title {
		}
    &__subTitle-container{
      height: 120px;
    }
		&__subtitle {
      font-size: 16px;
		}
}
  		
}
@media(max-width: 660px){

  .footer {
    &__logo{
    }
  
		&__info {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      grid-template-rows: 130px 1fr 110px;
      grid-column-gap: 0px;
      grid-row-gap: 0px;
      &>:nth-child(1){
        grid-area: 1 / 1 / 2 / 2;
        &>:last-child{
          height: 90px;
        }
      }
      &>:nth-child(2){
        grid-area: 1 / 2 / 2 / 3;
        &>:last-child{
          height: 90px;
        }
      }
      &>:nth-child(3){
        grid-area: 2 / 1 / 3 / 2;
      }
      &>:nth-child(4){
        grid-area: 2 / 2 / 3 / 3;
      }
      &>:nth-child(5){
        grid-area: 3 / 1 / 4 / 3;
        &>:last-child{
          height: 70px;
        }
      }
		}
     
		
    &__picture{
    }

		&__info {
		}

		&__column {
		}

		&__title {
      font-size: 16px;
      font-weight: 600;
      height: 40px;
		}
    &__subTitle-container{
    }
		&__subtitle {
      font-size: 16px;
		}
}
  		
}
@media(max-width: 500px){

  .footer {
    &__logo{
    }
  
		&__info {
      display: block;
      &>:nth-child(1){
        &>:last-child{
          height: auto;
        }
      }
      &>:nth-child(2){
        grid-area: 1 / 2 / 2 / 3;
        &>:last-child{
          height: auto;
        }
      }
      &>:nth-child(3){
        &>:last-child{
          height: auto;
        }
      }
      &>:nth-child(4){
        &>:last-child{
          height: auto;
        }
      }
      &>:nth-child(5){
        &>:last-child{
          height: auto;
        }
      }

		}
     
    &__picture{
    }

		&__info {
		}

		&__column {
      position: relative;
      overflow: hidden;

		}

		&__title {
      font-size: 16px;
      font-weight: 600;
      padding: 10px 0;
   
		}
    &__title--active{
      background-color: $bodyButton;
      z-index: 2;
    }

    &__subTitle-container{
      display: none;
    }
    &__subTitle-container--active{
      padding: 15px 0 5px;
      display: block;
      animation-name: show-text;
      animation-duration: 500ms;
      z-index: 1;
    }

		&__subtitle {
      font-size: 16px;
      
		}
}
  		
}
@keyframes show-text{
  0%{
    margin-top: -100px;
  }
  100%{
    margin-top:0
  }
}

